header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 60px;
    position: sticky;
    background-color: white;
    top: 0px;
    nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .logo {
            white-space: nowrap;
            font-size: 1.8rem; }
        ul {
            list-style-type: none;
            li {
                margin: 0px 20px;
                a {
                    font-size: 1.2rem; } } } }
    .cartLink {
        widows: 30%;
        font-size: 1.2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        img {
            margin-left: 5px; } }
    .numberOfItems {
        margin-left: .4rem;
        text-align: center;
        background-color: red;
        color: white;
        width: 1.5rem;
        height: 1.5rem;
        border-radius: 50%; } }

@media screen and (min-width: 1024px) {
    header {
        nav {
            ul {
                display: flex; }
            .burger {
                display: none; } } } }

@media screen and (max-width: 1023px) {
    header {
        nav {
            width: 55%;
            .hidden {
                display: none; }
            ul {
                position: absolute;
                top: 60px;
                left 0px {}
                right: 0px;
                width: 100%;
                height: 40vh;
                background-color: rgba(0, 0, 0, .9);
                display: grid;
                align-items: center;
                justify-content: center;
                text-align: center;
                li {
                    a {
                        color: white; } } }
            .burger {
                .line1,.line2,.line3 {
                    margin: 5px;
                    width: 20px;
                    height: 3px;
                    background-color: black; } }
            .burgerTransform {
                .line1 {
                    transform: rotate(-45deg) translate(-5px,6px); }
                .line2 {
                    display: none; }
                .line3 {
                    transform: rotate(45deg); } } } } }
